
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import LoadMore from 'vux/src/components/load-more/index.vue'

import scrollTop from '@/components/scrollTop.vue'
const json = {code: 'b2'}
let json01 = {
pname: '',
cname: '',
rname: ''
}
export default {
name: '',
components: {
Group,
XAddress,
LoadMore,
scrollTop
},
data () {
return {
onLoadShow: true,
bannerImg: '',
lists: [],
addressData: ChinaAddressData,
addressValue: [],
pname: '省',
cname: '市',
rname: '区'
}
},
created () {
json01 = {
pname: '',
cname: '',
rname: ''
}
this.getBanner()
this.getLists()
},
methods: {
async getBanner () {
let result = await this.axios.post('/web/adlist', json)
this.bannerImg = this.base_img + result.data[0].img
},
async getLists () {
let result = await this.axios.post('/web/storelist', json01)
if (result.success) {
let data = result.data
data.map((item, index) => {
item.fullAddress = item.pname + item.cname + item.rname + item.address
item.coordinate = item.coordinate.split(',')
})
this.lists = data
}
},
search () {
json01 = {
pname: this.addressValue[0],
cname: this.addressValue[1],
rname: this.addressValue[2]
}
this.getLists()
},
onShadowChange (ids, names) { // 省市区改变
this.pname = names[0]
this.cname = names[1]
this.rname = names[2]
},
show () {
this.onLoadShow = false
},
onHide (bool) {
if (!bool && (this.pname === '省')) { // 如果取消
this.onLoadShow = true
}
},
addressLink (item) {
console.log(item)
let address = 'https://uri.amap.com/marker?position=' + item.coordinate[0] + ',' + item.coordinate[1] + '&name=' + item.fullAddress
window.open(address)
}
}
}



